    fetch(url, {})
      .then(res => {
        return res.json();
      }).then(formData => {
        let edmLength = formData.data.edm.items.length;
        // 
        if (edmLength != 2) {
          alert('');
          window.location.href = '/';
        } else {
          this.Packages = formData.data.edm;
        }
      })

